  const submitForm = async (e) => {
    e.preventDefault();

    //ADD A NEW EVENT
    if (modalButton == "Add") {
      let validate = validateInputs(inputs);
      if (!validate.valid) {
        setError(true);
        setErrorMessage(validate.msg);
        return;
      }
      setError(false);

      let req = await request.post(config.path.events.addEvent, inputs, true);
      if (req.err) {
        setError(true);
        setErrorMessage(req.error.message);
        return;
      }
      setSuccess(true);
      setSuccessMessage("Successfully Created Event.");

      getEventData();

      setTimeout(() => {
        resetState();
      }, 2000);
    }
    //EDIT A EVENT
    else {
      e.stopPropagation();

      let validate = validateInputs(inputs);
      if (!validate.valid) {
        setError(true);
        setErrorMessage(validate.msg);
        return;
      }
      setError(false);
      // console.log("first");

      setSuccessModalOpen(true);
    }
  };

  //STORE INPUTS FROM FORMS INPUTS
  const handleChange = (e) => {
    setInputs((prevState) => ({
      ...prevState,
      [e.target.id]: e.target.value,
    }));
  };

  //CLEAR ALL TEXT FIELDS
  const clearInputs = () => {
    Array.from(document.querySelectorAll("input")).forEach(
      (input) => (input.value = "")
    );

    Array.from(document.querySelectorAll("select")).forEach(
      (select) => (select.selectedIndex = 0) //reset input
    );
  };

  //RESET SPECIFIC STATE VARIABLES
  const resetState = () => {
    // console.log("reset");
    setFeedbackModalOpen(false);
    setSuccess(false);
    setError(false);
    setSuccessModalOpen(false);
    setModalTitle("Add New Event");
    setModalButton("Add");
    setInputs({
      eventName: "",
      eventStart: "",
      eventEnd: "",
    });
    setEventInfo({
      start: "",
      end: "",
      name: "",
      id: "",
    });
    clearInputs();
  };

  //FUNCTION TO CHECK TOKENS
  const checkToken = async () => {
    // console.log("checkToken()");
    let authToken = localStorage.getItem("auth-token");
    if (isJwtExpired(authToken)) {
      // console.log("experied");
      localStorage.setItem("auth-token", null);
      dispatch({
        type: "LOGOUT",
      });
      // console.log("login out...");
      navigateTo("/signin");
    }
  };

  const isToday = (date) => {
    const day = new Date(year, month, date);
    return today.toDateString() === day.toDateString() ? true : false;
  };

  const getEvents = (date) => {
    return events.filter(
      (e) =>
        new Date(e.eventStart).toDateString() ===
        new Date(year, month, date).toDateString() || new Date(e.eventEnd).toDateString() ===
        new Date(year, month, date).toDateString() 
    );
  };

  const eventColor = (color) => {
    switch (color) {
      case "sky":
        return "text-white bg-sky-500";
      case "indigo":
        return "text-white bg-indigo-500";
      case "yellow":
        return "text-white bg-yellow-500";
      case "emerald":
        return "text-white bg-emerald-500";
      case "red":
        return "text-white bg-rose-400";
      default:
        return "";
    }
  };

  const getDays = () => {
    const days = new Date(year, month + 1, 0).getDate();

    // starting empty cells (previous month)
    const startingDayOfWeek = new Date(year, month).getDay();
    let startingBlankDaysArray = [];
    for (let i = 1; i <= startingDayOfWeek; i++) {
      startingBlankDaysArray.push(i);
    }

    // ending empty cells (next month)
    const endingDayOfWeek = new Date(year, month + 1, 0).getDay();
    let endingBlankDaysArray = [];
    for (let i = 1; i < 7 - endingDayOfWeek; i++) {
      endingBlankDaysArray.push(i);
    }

    // current month cells
    let daysArray = [];
    for (let i = 1; i <= days; i++) {
      daysArray.push(i);
    }

    setStartingBlankDays(startingBlankDaysArray);
    setEndingBlankDays(endingBlankDaysArray);
    setDaysInMonth(daysArray);
  };

  const getEventData = async () => {
    checkToken();
    setLoading(true);

    let eventData = await request.get(config.path.events.getAllEvents);

    // console.log("eventData: ", eventData);

    //FOR EACH EVENT CONVERT DATES
    eventData.forEach((event) => {
      event.eventStart = new Date(event.eventStart);
      event.eventEnd = new Date(event.eventEnd);
    });
    // console.log("eventData: ", eventData);
    setEvents(eventData);
    setLoading(false);
  };

  const viewEvent = async (e, eventData) => {
    e.stopPropagation();

    // console.log("eventData: ", eventData);

    setEventInfo({
      name: eventData.eventName,
      start: new Date(eventData.eventStart).toString(),
      end: new Date(eventData.eventEnd).toString(),
      id: eventData._id,
      editable: eventData.editable,
    });

    setInfoModalOpen(true);
  };

  const editEvent = async (e) => {
    e.stopPropagation();

    document.getElementById("eventName").value = eventInfo.name;
    document.getElementById("eventStart").value = new Date(eventInfo.start)
      .toISOString()
      .split("T")[0];
    document.getElementById("eventEnd").value = new Date(eventInfo.end)
      .toISOString()
      .split("T")[0];

    setInputs({
      eventName: eventInfo.name,
      eventStart: eventInfo.start,
      eventEnd: eventInfo.end,
    });

    setModalTitle("Edit Event");
    setModalButton("Update");

    setFeedbackModalOpen(true);
    setInfoModalOpen(false);
  };

  const updateEvent = async (e) => {
    e.stopPropagation();
    let req = await request.patch(
      config.path.events.editEvent + "/" + eventInfo.id,
      inputs,
      true
    );
    if (req.err) {
      setError(true);
      setErrorMessage(req.error.message);
      return;
    }
    setSuccess(true);
    setSuccessMessage("Successfully Edited Event.");

    getEventData();

    setTimeout(() => {
      resetState();
    }, 2000);
  };

  const deleteEvent = async (e) => {
    e.preventDefault();

    let req = await request.delete(
      `${config.path.events.deleteEvent}/${eventInfo.id}`
    );
    if (req.err) {
      return;
    }

    getEventData();

    setTimeout(() => {
      resetState();
      setDangerModalOpen(false);
    }, 1000);
  };

  useEffect(() => {
    getDays();
   getEventData();
  }, []);

  //USE EFFECT TO LOGOUT IF TOKEN EXPIRED
  useEffect(async () => {
    checkToken();
  }, [request]);

