import React, { useState, useRef, useEffect } from "react";
import { Link } from "react-router-dom";
import Transition from "../utils/Transition";
import { useMsal } from "@azure/msal-react";
import Iconify from '@iconify/iconify';
import UserAvatar from "../images/user-avatar-32.png";
import c3Image from "../images/c3-122x69.png";
import { useNavigate } from "react-router-dom";
function DropdownProfile({ align }) {
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const [menuTitle, setMenuTitle] = useState("Employee");
  const { instance } = useMsal();
  const trigger = useRef(null);
  const dropdown = useRef(null);

  // close on click outside
  useEffect(() => {
    let adminTag = localStorage.getItem("adminTag"); //GET TAG/////////////////////////////
    if (adminTag == "valid") {
      setMenuTitle("Administrator");
    }

    const clickHandler = ({ target }) => {
      if (!dropdown.current) return;
      if (
        !dropdownOpen ||
        dropdown.current.contains(target) ||
        trigger.current.contains(target)
      )
        return;
      setDropdownOpen(false);
    };
    document.addEventListener("click", clickHandler);
    return () => document.removeEventListener("click", clickHandler);
  });

  // close if the esc key is pressed
  useEffect(() => {
    const keyHandler = ({ keyCode }) => {
      if (!dropdownOpen || keyCode !== 27) return;
      setDropdownOpen(false);
    };
    document.addEventListener("keydown", keyHandler);
    return () => document.removeEventListener("keydown", keyHandler);
  });

  return (
    
    <div className="relative inline-flex">
      <button
        ref={trigger}
        className="inline-flex justify-center items-center group"
        aria-haspopup="true"
        onClick={() => setDropdownOpen(!dropdownOpen)}
        aria-expanded={dropdownOpen}
      >
        <img
          className="w-10 rounded-full pt-1" //w-8 h-8
          src="https://i.ibb.co/fvGBWTW/c3-small-dark.png"
          // width="64"
          //height="32"
          alt="User"
        />
        <div className="flex items-center truncate">
          <span className="truncate ml-2 text-sm font-medium group-hover:text-zinc-800">
            Convergenc3
          </span>
          {/*  <svg
            className="w-3 h-3 shrink-0 ml-1 fill-current text-zinc-400"
            viewBox="0 0 12 12"
          >
            <path d="M5.9 11.4L.5 6l1.4-1.4 4 4 4-4L11.3 6z" />
          </svg>*/}
        </div>
      </button>

      <Transition
        className={`origin-top-right z-10 absolute top-full min-w-44 bg-white border border-zinc-200 py-1.5 rounded shadow-lg overflow-hidden mt-1 ${
          align === "right" ? "right-0" : "left-0"
        }`}
        show={dropdownOpen}
        enter="transition ease-out duration-200 transform"
        enterStart="opacity-0 -translate-y-2"
        enterEnd="opacity-100 translate-y-0"
        leave="transition ease-out duration-200"
        leaveStart="opacity-100"
        leaveEnd="opacity-0"
      >
        <div
          ref={dropdown}
          onFocus={() => setDropdownOpen(true)}
          onBlur={() => setDropdownOpen(false)}
        >
          <div className="pt-0.5 pb-2 px-3 mb-1 border-b border-zinc-200">
            <div className="font-medium text-zinc-800">Convergenc3</div>
            <div className="text-xs text-zinc-500 italic">{menuTitle}</div>
          </div>
          <ul>
            {/*<li>
              <Link
                className="font-medium text-sm text-indigo-500 hover:text-indigo-600 flex items-center py-1 px-3"
                to="/settings"
                onClick={() => setDropdownOpen(!dropdownOpen)}
              >
                Settings
              </Link>
      </li>*/}
            <li>
              <Link
                className="font-medium text-sm text-rose-500 hover:text-rose-600 flex items-center py-1 px-3"
                to="/signin"
                onClick={() => {
                  localStorage.clear();
                  instance.logoutPopup({
                    postLogoutRedirectUri: "/signin",
                    mainWindowRedirectUri: "/signin",
                  });
                  setDropdownOpen(!dropdownOpen);
                }}
              >
                Sign Out
              </Link>
            </li>
          </ul>
        </div>
      </Transition>
    </div>
  );
}

export default DropdownProfile;
